.promo {
  height: 860px;
  background-color: $grey;
  background-image: url("../img/aleyna-rentz.jpg");
  background-position: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.promo__title {
  width: 325px;
  margin: 0;
  padding: 0;
  font-size: 55px;
  font-weight: 500;
  line-height: 80px;
  color: $white;
}

.promo__text {
  width: 360px;
  margin: 0;
  margin-top: 30px;
  padding: 0;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: -0.47px;
  color: $white;
}

.promo__btn {
  position: relative;
  width: 340px;
  margin-top: 39px;
  padding-top: 24px;
  padding-bottom: 25px;
  padding-left: 30px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.promo__btn::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 272px;
  width: 14px;
  height: 18px;
  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  transform: translateY(-50%);
}